@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
    -webkit-filter: grayscale(100%) blur(1px);
            filter: grayscale(100%) blur(1px);
}

.home-container{
    height: 100vh;
    width: 100%;
    /* display: flex; */
    flex-direction: column;
    align-items: center;
    object-fit: contain;
}

.home-container > h1{
    font-family: 'Montserrat' sans-serif;
    padding-top: 100px;
    padding-bottom: 50px;
    color:#0094e3;
    max-width: 600px;
    font-size: 90px;
    align-items: center;
    text-align: center;
    margin: 0 auto;
}

.home-container > p{
    font-family: 'Montserrat' sans-serif;
    padding-top: 50px;
    padding-bottom: 30px;
    color: white;
    font-size: 24px;
    align-items: center;
    text-align: center;
    margin: 0 auto;
}

button1 {
    font-family: 'Montserrat' sans-serif;
    margin-left: 45%;
    margin-right: 40%;
    font-weight: 400;
    padding: 15px 20px;
    background-color: transparent;
    font-size: 24px;
    color: #0094e3;
    border: 2px solid #0094e3;
    align-items: center;
    text-align: center;
  }
.LangButton{
    height:100px;
    width:200px;
    margin: 20px;
    background-color: #ffffff;
    border: 0.08em solid;
    color: while;
    padding: 10px;
    text-align: center;
    display: grid;
    font-size: 16px;
}
* {
  box-sizing: border-box;
}

body {
  background-image: url("https://images.unsplash.com/random");
  background-size: cover;
  background-position: center center;
  /* display: flex; */
  font-family: "Lato", sans-serif;
  font-size: 140%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0;
  min-height: 100vh;
}

h4 {
  margin: 0 0 5px;
}

progress {
  width: 100%;
}

p {
  line-height: 1.5;
}

.container .text {
  /* display: flex; */
  flex-wrap: wrap;
}

.container input {
  padding: 12px 15px;
  font-size: 20px;
  margin: 10px 0;
  width: 100%;
}

.start-btn {
  border: 0;
  background-color: #000;
  color: #fff;
  font-size: 16px;
  padding: 12px 15px;
}

.wpm {
  position: fixed;
  top: 20px;
  right: 20px;
  text-align: right;
}

.green {
  color: green;
}

.red {
  background-color: rgba(255, 0, 0, 0.5);
}

.underline {
  border-bottom: 1px solid #000;
}

.word {
  font-size: 20px;
  margin: 2px;
}

.share {
  color: #38a1f3;
}

@media screen and (max-width: 650px) {
  p {
    line-height: 1.3;
  }

  .container {
    margin-bottom: 50px;
    padding: 10px;
    width: 100%;
  }

  .wpm {
    display: none;
  }
}

